export default {
	props: {
		// 是否可见
		visible: Boolean,
		// 标题
		title: String,
		// 宽度
		width: {
			type: String,
			default: "80%",
		},
		// 点击遮罩层是否关闭
		closeOnClickModal: {
			type: Boolean,
			default: true,
		},
		// 关闭前
		beforeClose: Boolean,
	},

	methods: {
		close(action) {
			const done = () => {
				this.$emit("update:visible", false);
			};

			if (this.beforeClose) {
				this.$emit("close", { action, done });
			} else {
				this.$emit("close", { action });
				done();
			}
		},

		modalClose() {
			if (!this.closeOnClickModal) {
				return false;
			}

			this.close("close");
		},
	},
};
